.products {
    width: 100%;
    display: flex;    
    justify-content: center;
    align-items: center;      
  }
  
  .products ul {
    display: grid;   
    grid-auto-rows: 22rem;     
    grid-template-columns: repeat(
      auto-fit,
      minmax(min(100%, 16rem),
        1fr
      )
    );
    gap: 1rem;  
    
  }
  
  .products li {
    display: flex;
    flex-direction: column;    
    justify-content: center;
    align-items: center;
    gap: 1rem;
    box-shadow: 0 0 10px 10px rgba(0, 0, 0, .1);
    border-radius: 4px;
    background: #111;
    color: #fff;
    padding: 1rem;
  }
  
  .products h3 {
    margin: 0;
  }
  
  .products span {
    font-size: 1rem;
    opacity: .9;
  }
  
  .products img {
    border-radius: 4px;
    width: 100%;
    aspect-ratio: 16/9;
    display: block;
    object-fit: cover;
    background: #fff;
  }
  
  .products .card{   
    overflow: hidden;      
    transition: all 0.25s;
  }
  
  .products .card:hover{
    transform: translateY(-15px);
    box-shadow: 0 12px 16px rgba(0, 0, 0, 0.2);
  }